@import "../../../index.scss";

.Toggle{
    display: flex;
    width:80px;
    height: 22px;
    border-radius: 5px;
    border: solid 1px #d3d3d3;
    cursor:pointer;
.Toggler{
    width:50%;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #999999;
    padding: 10px;
    //border-radius: 0px;
}
.togglerchange{
    width: 50%;
    background-color: $PrimaryBgColor;
    border-radius: 5px;
    color: #ffffff;
}

}

