@import '../../../index.scss';
.tab-main-div {
  height: 100%;
  width: 21%;
  position: fixed;
  margin-top: 0px;
  padding-left: 0px;
}

.main-tab {
  padding: 30px 30px 25px 29px;
}

.tab-panel {
  margin-left: 27.1%;
  background-color: #fff;
}

.tablist {
  list-style-type: none;
  height: 48.5px;
  font-size: 14px;
  font-weight: 600;
  color: $PrimaryTextColor;
  display: flex;
  align-items: center;
  padding-left: 28px;
  border: 1px solid #e7e8f7;
  border-bottom: none;
  background-color: #fff;
  cursor: pointer;
}

.tablist:last-child {
  border-bottom: 1px solid #e7e8f7;
}

.react-tabs__tab--selected {
  background-color: #f8f8f8;
  color: #000000;
}
.tab-head-div {
  color: #666666;
  padding-left: 10%;
  padding-bottom: 6%;
  padding-top: 1%;
}
