.Acc_Class {
    height: 51px;
    width: 100%;
    background-color: #f8f8f8;   
    border:  none;
    display: flex;
    align-items: center;
    padding: 0px 17.8px 0px 15.5px;
    background-color: #fff;
    border-top: 1px solid #e7e8f7;
}
.acc-active {
    background-color: #f8f8f8;
    border-top: none;
}
.ACC_Name {
    border:  none;
}
.acc-chevron {
    margin-left: auto;
}

.example-enter {
    height: 0px;
    overflow: hidden;
}

.example-leave {
    height: 0px;
   overflow: hidden;
}
.example-enter.example-enter-active {
    height: 350px;
    transition: height .7s ease;
}

.example-leave.example-leave-active {
    height: 0px;
    transition: height .6s ease-in-out;
}
.Rectangle-116 {
    width: 100%;
    height: 57px;
    border: solid 1px #e7e8f7;
    background-color: #f8f8f8;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    font-size: 14px;
}

.station-datalist input[type=text]:disabled {
    opacity: 1;
    //background-color: red;
    cursor: not-allowed;
    }
    
.none input[type=text]:disabled {
    opacity: 1;
   
    cursor: not-allowed;
    }